import React, { Component } from 'react';
import '../../assets/sass/research.scss';

import cps_image from '../../assets/images/work/cps_example.jpg'
import representations from '../../assets/images/work/representations.png'

export default function Research() {
    return (
    <section
    className="resume-section p-3 p-lg-5 d-flex justify-content-center"
    id="research"
    >
    <div className="w-100">
      <h2 className="mb-5">Practical Applications</h2>

      <div className="resume-item d-flex flex-column flex-md-row justify-content-between mb-5">
        <div className="resume-content">
          <h3 className="mb-2">Assurance Monitoring for Cyber-physical Systems</h3>
          <h5 className="mb-0">What is a cyber-physical system? </h5>
          <p className="mb-3">
            <img src={cps_image} class="rounded float-right research_image_portrait" alt=""></img>
            Cyber-Phsyical Systems (CPS) integrate sensing, computation and networking which are parts of physical processes and are
            monitored or controlled using computer algorithms. In CPS physical and software components are deeply intertwined.
            Many modern products in major industrial sectors, such as:
            <ul>
              <li>Automotive</li>
              <li>Avionics</li>
              <li>Medical Devices</li>
              <li>Power Systems</li>
              <li>Smart Cities</li>
            </ul>
            already are or rapidly becoming CPS driven by new requirements and competitive pressures.
            These are safety critical fields where the introduction of autonomous components are challenging as potential incorrect decisions can have very serious consequences.
            For such components to safely be integrated in CPS they need to be complemented by methods and practices that will assure the safe operation of the system.
          </p>
          <h5 className="mb-0">Machine Learning and Challenges </h5>
          <p className="mb-2">
          Semi-autonomous and autonomous vehicles are a very significant domain and opportunity for CPS. 
          Modern vehicles are equipped with a number of sensors, like cameras and Light Detection and Ranging (LiDAR) sensors, 
          to either achieve fully autonomous driving or assist the human operator in dangerous scenarios. 
          Even though all these sensors produce a lot of information about the environment and the surroundings of the vehicle, 
          it can be challenging and tedious to have some desired control actions for all possible scenarios on continuously changing environments. 
          This shows the need for integrating machine learning components in CPS that can deal with decision-making in dynamic environments.
          </p>
          <p className="mb-2">
          The most commonly used machine learning component is the Deep Neural Networks (DNN) because of their high knowledge capacity and their ability to receive and make decisions for high-dimensional inputs. 
          They achieve this by transforming the inputs to a number of sequential feature layers of less-and-less abstraction until a classifier can be defined in a lower dimensional feature space. 
          The mapping between the different feature layers is learned using labeled training data and the knowledge of a particular DNN model is stored in its weight parameters. 
          Modern architectures can have hundreds of layers and are parameterized using millions of values. 
          This makes it challenging for DNNs to be used in safety critical CPS application because its hard to understand how a DNN makes a particular decision, or reason about a decision, and how confident a decision is. 
          This problem with DNNs is also called <em>non transparency</em>. DNNs commonly use a <em>softmax</em> layer to provide probability-like outputs, meaning the output for each class is in [0,1] and the sum of the outputs of all possible classes is 1. 
          However, these probabilities are typically overconfident even for inputs coming from the same distribution as the training data and they cannot be used as reliable confidence measures.
          </p>
          <p className="mb-2">
          Deploying a CPS with machine learning components in the real world comes with challenges that are hard to foresee in design-time.
          CPS usually operate in highly dynamic environments where unknown scenarios may appear. 
          Even using very deep DNN architectures it is hard to have training data for every possible input a system may face during testing. 
          The unpredictability in the real world will force the system to work under different conditions. For example, an autonomous vehicle has to drive safely in different weather conditions. 
          Different operating conditions may introduce different kind of noise into the input data that will make confident decisions harder.
          </p>
          <p className="mb-3">
          <strong>Problems we consider are:</strong>
          <ul>
              <li>How do we compute well-calibrated confidence metrics with each decision made by a machine learning for effective integration in CPS?</li>
              <li>How to minimize the human intervention and maximize the safe operation time?</li>
              <li>How to process and take confident decisions on high-dimensional data?</li>
          </ul>
          </p>
          <h5 className="mb-0">Research Contributions </h5>
          <p className="mb-2">
          Our objective is to complement the prediction of DNNs
          with a computation of confidence. We consider DNNs used for classification in CPS.
          In addition to the class prediction, we compute prediction sets of classes that are guaranteed to include
          the correct class with the desired significance level.
          We focus on computationally efficient algorithms that can be used for real-time monitoring. 
          An efficient and robust approach must ensure a small and well-calibrated error rate while limiting 
          the number of alarms. This enables the design of monitors which can ensure a bounded small error 
          rate while limiting the number of inputs for which an accurate prediction cannot be made.
          </p>
          <p className="mb-2">
          <img src={representations} class="rounded float-right research_image_landscape" alt=""></img>
          We propose the transformation of the high-dimensional inputs into lower-dimensional embedding representations that can be handled efficiently by the assurance monitor. 
          For these representations to be used for monitoring they need to be in a form in which similarity between different data points can be defined. 
          We use distance metric learning techniques to compute embedding representations such that the euclidean distance between them is a metric of similarity between the original inputs. 
          Moreover, autonomous systems are desirable to operate safely for as long as possible and require as little intervention by humans as possible. 
          We propose an optimization method to minimize the instances where a confident decision cannot be made for a given input. Our proposals have been implemented and evaluated.
          </p>
        </div>
      </div>



    </div>
    </section>
    )
}